<template>
  <div class="step-2">
    <card-vue>
      <template v-slot:text>
        <p>
          Welcome Mr. <b>{{ data.name }}</b
          >, we are happy to serve you<br />
          can you provide us your children data?
        </p>
      </template>
      <template v-slot:elements>
        <v-col cols="auto">
          <Dialog @addChild="addChild" class="mb-2" />
        </v-col>
        <ChildCard
          v-for="(child, index) in children"
          :name="child.name"
          :gender="child.gender"
          :date="child.date"
          :key="index"
          @deleteChild="deleteChild"
        />
      </template>
    </card-vue>
  </div>
</template>

<script>
import CardVue from "@/components/global/Card.vue";
import Dialog from "@/components/Parent/Dialog.vue";
import ChildCard from "@/components/Parent/ChildCard.vue";

export default {
  components: { CardVue, Dialog, ChildCard },
  props: ["data"],
  data() {
    return {
      name: "",
      children: [],
    };
  },
  methods: {
    nextStep() {
      this.$emit("next-step", 3);
    },
    addChild(child) {
      this.children.push(child);
    },
    deleteChild(removedChild) {
      this.children = this.children.filter(
        (child) => child.name !== removedChild.name
      );
    },
  },
};
</script>

