<template>
  <div class="step-1">
    <card-vue>
      <template v-slot:text>
        <p>Welcome to daycare system, shall I know your name?</p>
      </template>
      <template v-slot:elements>
        <v-text-field
          v-model="name"
          label="Parent Name"
          required
          hide-details
          class="mx-auto mt-2 w-50"
          @keydown.enter="finishStep"
        ></v-text-field>
        <v-btn
          class="btn text-none mt-4 w-50"
          color="blue-darken-2"
          size="large"
          variant="flat"
          @click="finishStep"
        >
          Continue
        </v-btn>
      </template>
    </card-vue>
  </div>
</template>

<script>
import CardVue from "@/components/global/Card.vue";

export default {
  components: { CardVue },
  data() {
    return {
      name: "",
    };
  },
  methods: {
    finishStep() {
      this.$emit("finish-step", { step: 2, name: this.name });
    },
  },
};
</script>

<style scoped lang="scss"></style>
