<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent width="1024">
      <template v-slot:activator="{ props }">
        <v-btn
          v-bind="props"
          append-icon="mdi-account-circle"
          class="btn text-none mt-4"
          color="blue-darken-2"
          size="large"
          variant="flat"
        >
          <template v-slot:append>
            <v-icon color="white"></v-icon>
          </template>
          <p>Add</p>
        </v-btn>
      </template>
      <v-card color="blue-accent-2">
        <v-card-title>
          <p class="text-h5">Add a child</p>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="12" md="6">
                <v-text-field
                  label="Name"
                  required
                  v-model="child.name"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="6">
                <v-select
                  v-model="child.gender"
                  :items="gender"
                  :rules="[(v) => !!v || 'Gender is required']"
                  label="Gender"
                  required
                ></v-select>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" sm="12" md="6">
                <date-picker :title="dateText" @date="getDate" />
              </v-col>
            </v-row>
          </v-container>
          <p>*indicates required field</p>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn variant="text" @click="dialog = false"> Close </v-btn>
          <v-btn variant="text" @click="addChild"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
import DatePicker from "../global/DatePicker.vue";

export default {
  components: { DatePicker },
  data() {
    return {
      child: { name: null, gender: null, date: null },
      dialog: false,
      gender: ["Male", "Female"],
      dateText: "Date of birth",
    };
  },
  methods: {
    addChild() {
      const newChild = { ...this.child }; // Create a new child object
      if (newChild.date === null) {
        alert("Please select date of birth of the child");
        return;
      }
      this.$emit("addChild", newChild); // Emit the new child object
      this.dialog = false;
      // Reset the child object for the next addition
      this.child.name = null;
      this.child.gender = null;
      this.child.date = null;
    },
    getDate(date) {
      this.child.date = date;
    },
  },
};
</script>
