<template>
  <div
    class="card-container justify-content-center align-items-center text-center"
  >
    <v-card
      class="mx-auto rounded-xl card"
      title="Daycare"
      color="blue-accent-2"
    >
      <template v-slot:prepend>
        <v-avatar color="blue-darken-2">
          <v-icon icon="mdi-school"></v-icon>
        </v-avatar>
      </template>
      <template v-slot:append>
        <v-btn icon color="transparent">
          <v-icon class="text--white">mdi-close</v-icon>
        </v-btn>
      </template>
      <v-card-text>
        <div class="card-text typing-demo text-left" ref="typingDemo">
          <slot name="text"></slot>
        </div>
        <transition name="scale">
          <div>
            <slot name="elements" class="w-100 text-center"></slot>
          </div>
        </transition>
      </v-card-text>
    </v-card>
    <!-- <img :src="require('@/assets/cloud.png')" alt="cloud" class="cloud-1" />
    <img :src="require('@/assets/cloud.png')" alt="cloud" class="cloud-2" /> -->
    <img :src="require('@/assets/cloud.png')" alt="cloud" class="cloud-3" />
    <img :src="require('@/assets/cloud.png')" alt="cloud" class="cloud-4" />
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.adjustWidth();
  },
  methods: {
    adjustWidth() {
      // Get the width of the text inside the typing-demo element
      const textWidth = this.$refs.typingDemo.scrollWidth;
      // Set the width of the typing-demo element to the width of the text
      this.$refs.typingDemo.style.width = `${textWidth}px`;
    },
  },
};
</script>

<style scoped>
.card-container {
  position: relative;
}

.cloud-1 {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: -100px;
  margin-top: -100px;
  width: 200px;
  transform: rotate(-20deg);
  animation: floatCloud 2s ease-in-out infinite alternate;
}

.cloud-2 {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: -100px;
  margin-top: -100px;
  width: 200px;
  transform: rotate(20deg);
  animation: floatCloud 1.7s ease infinite alternate;
}

.cloud-3 {
  position: absolute;
  bottom: 0;
  left: 0;
  margin-left: -100px;
  margin-bottom: -100px;
  width: 200px;
  transform: rotate(-20deg);
  animation: floatCloudBottom 2s ease-in-out infinite alternate;
}

.cloud-4 {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-right: -100px;
  margin-bottom: -100px;
  width: 200px;
  transform: rotate(20deg);
  animation: floatCloudBottom 1.7s ease infinite alternate;
}
@keyframes floatCloud {
  0% {
    transform: translate(0, -10%);
  }
  100% {
    transform: translate(0, 10%);
  }
}
@keyframes floatCloudBottom {
  0% {
    transform: translate(0, 10%);
  }
  100% {
    transform: translate(0, -10%);
  }
}
.card-text {
  font-size: 22px;
  line-height: 2em;
}

.typing-demo {
  animation: typing 2s, blink 0.5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
  word-wrap: break-word; /* Added property */
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  0% {
    border-color: transparent;
  }
}

.scale-enter-active,
.scale-leave-active {
  transition: 400ms ease all;
}

.scale-leave-to {
  opacity: 0;
  transform: scale(1.5);
}
.scale-enter-from {
  opacity: 0;
  transform: scale(0.5);
}
</style>
