<template>
  <div>
    <AppBar />
    <v-container
      class="container-background d-flex justify-center align-center"
      fluid
      style="height: calc(100vh - 64px)"
    >
      <div class="container-content">
        <transition name="scale">
          <div v-if="step === 1" class="step-container">
            <step-1-vue @finish-step="stepOne" />
          </div>
          <div v-else-if="step === 2" class="step-container">
            <step-2-vue :data="step2Data" />
          </div>
          <div v-else-if="step === 3" class="step-container">
            <step-3-vue />
          </div>
          <div v-else class="step-container"></div>
        </transition>
      </div>
    </v-container>
  </div>
</template>

<script>
import Step1Vue from "@/components/Parent/Step1.vue";
import Step2Vue from "@/components/Parent/Step2.vue";
import Step3Vue from "@/components/Parent/Step3.vue";
import AppBar from "@/components/global/AppBar.vue";

export default {
  name: "ParentView",
  components: { Step1Vue, Step2Vue, Step3Vue, AppBar },
  data() {
    return {
      step: 1,
      step2Data: "",
    };
  },
  methods: {
    stepOne(data) {
      this.step = data.step;
      this.step2Data = data;
    },
  },
};
</script>

<style scoped>
.step-container {
  width: 100%;
  z-index: 1;
}

.scale-enter-active,
.scale-leave-active {
  transition: 400ms ease all;
}

.scale-leave-to {
  opacity: 0;
  transform: scale(1.5);
}
.scale-enter-from {
  opacity: 0;
  transform: scale(0.5);
}

.container-background {
  position: relative;
  overflow: hidden;
}

.container-background::before {
  content: "";
  position: absolute;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  background-image: url("https://i.pinimg.com/originals/a5/33/d4/a533d4095fce9e06452e235983eb21ff.jpg");
  background-size: cover;
  background-position: center;
  filter: blur(10px);
  z-index: 0;
  opacity: 0.9;
}
</style>
