<template>
  <v-dialog v-model="dialog" persistent class="text-center">
    <template v-slot:activator="{ props }">
      <v-btn
        v-bind="props"
        append-icon="mdi-calendar"
        class="btn text-none mt-4"
        color="blue-darken-2"
        size="large"
        variant="flat"
        @click="addChild"
      >
        <template v-slot:append>
          <v-icon color="white"></v-icon>
        </template>
        <p>{{ title }}</p>
      </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <p class="text-h5">Select a date</p>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-date-picker v-model="date" min="2018-01-01"></v-date-picker>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue-darken-1" variant="text" @click="submitDate">
          Submit
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  props: ["title"],
  data() {
    return {
      dialog: false,
      date: null,
      dateStr: null,
    };
  },
  methods: {
    parseDate(date) {
      if (!date) return null; // return null if date is not provided
      const parsedDate = new Date(date);
      const year = parsedDate.getFullYear();
      const month = String(parsedDate.getMonth() + 1).padStart(2, "0");
      const day = String(parsedDate.getDate()).padStart(2, "0");
      return `${year}-${month}-${day}`;
    },
    submitDate() {
      this.dialog = false;
      this.dateStr = this.parseDate(this.date);
      this.$emit("date", this.dateStr);
    },
  },
};
</script>
